import React, { ReactElement } from "react"
import { Link } from "gatsby"
import Layout from "../../components/layout"
import InlineCode from "../../components/inlineCode"
import Examples from "../../components/codeExamples"
import { ValidatorFunction, CodeExample } from "../../components/validatorFuncs"

function ComparisonFunctions(): ReactElement {
  return (
    <Layout
      title="Comparison functions"
      description="Validation functions for use on IComparable types"
    >
      <h1>Comparison Functions</h1>
      <p>
        These functions can be used on <InlineCode>IComparable</InlineCode>{" "}
        types.
      </p>
      <p>
        <ul className="list">
          {[
            "GreaterThan",
            "GreaterThanOrEqual",
            "LessThan",
            "LessThanOrEqual",
            "Range",
            "Compare",
          ].map((func) => (
            <li>
              <Link to={`/functions/comparison/#${func}`}>{func}</Link>
            </li>
          ))}
        </ul>
      </p>
      <hr className="my-6" />
      <ValidatorFunction
        name="GreaterThan"
        description="Ensures the value being validated is greater than a specified value."
        methods={[
          `GreaterThan<T>(T other)`,
          `GreaterThan<T, P>(Expression<Func<T, P>> selector, P other)`,
        ]}
        brokenRule="Value must be greater than '{other}'."
      >
        <CodeExample
          label="Using type instance."
          code={Examples.Comparison.GreaterThan.Example1}
        />
        <CodeExample
          label="Using selector expression."
          code={Examples.Comparison.GreaterThan.Example2}
        />
        <CodeExample
          label={
            <>
              Using <InlineCode>For</InlineCode> function.
            </>
          }
          code={Examples.Comparison.GreaterThan.Example3}
        />
      </ValidatorFunction>
      <hr className="my-6" />
      <ValidatorFunction
        name="GreaterThanOrEqual"
        description="Ensures the value being validated is greater than or equal to a specified value."
        methods={[
          `GreaterThanOrEqual<T>(T other)`,
          `GreaterThanOrEqual<T, P>(Expression<Func<T, P>> selector, P other)`,
        ]}
        brokenRule="Value must be greater than or equal to '{other}'."
      >
        <CodeExample
          label="Using type instance."
          code={Examples.Comparison.GreaterThanOrEqual.Example1}
        />
        <CodeExample
          label="Using selector expression."
          code={Examples.Comparison.GreaterThanOrEqual.Example2}
        />
        <CodeExample
          label={
            <>
              Using <InlineCode>For</InlineCode> function.
            </>
          }
          code={Examples.Comparison.GreaterThanOrEqual.Example3}
        />
      </ValidatorFunction>
      <hr className="my-6" />
      <ValidatorFunction
        name="LessThan"
        description="Ensures the value being validated is less than a specified value."
        methods={[
          `LessThan<T>(T other)`,
          `LessThan<T, P>(Expression<Func<T, P>> selector, P other)`,
        ]}
        brokenRule="Value must be less than '{other}'."
      >
        <CodeExample
          label="Using type instance."
          code={Examples.Comparison.LessThan.Example1}
        />
        <CodeExample
          label="Using selector expression."
          code={Examples.Comparison.LessThan.Example2}
        />
        <CodeExample
          label={
            <>
              Using <InlineCode>For</InlineCode> function.
            </>
          }
          code={Examples.Comparison.LessThan.Example3}
        />
      </ValidatorFunction>
      <hr className="my-6" />
      <ValidatorFunction
        name="LessThanOrEqual"
        description="Ensures the value being validated is less than or equal to a specified value."
        methods={[
          `LessThanOrEqual<T>(T other)`,
          `LessThanOrEqual<T, P>(Expression<Func<T, P>> selector, P other)`,
        ]}
        brokenRule="Value must be less than or equal to '{other}'."
      >
        <CodeExample
          label="Using type instance."
          code={Examples.Comparison.LessThanOrEqual.Example1}
        />
        <CodeExample
          label="Using selector expression."
          code={Examples.Comparison.LessThanOrEqual.Example2}
        />
        <CodeExample
          label={
            <>
              Using <InlineCode>For</InlineCode> function.
            </>
          }
          code={Examples.Comparison.LessThanOrEqual.Example3}
        />
      </ValidatorFunction>
      <hr className="my-6" />
      <ValidatorFunction
        name="Range"
        description="Ensures the value being validated is within (inclusive) a specified value."
        methods={[
          `Range<T>(T lower, T upper)`,
          `Range<T, P>(Expression<Func<T, P>> selector, P lower, P upper)`,
        ]}
        brokenRule="Value must be in range '{lower}' to '{upper}'."
      >
        <CodeExample
          label="Using type instance."
          code={Examples.Comparison.Range.Example1}
        />
        <CodeExample
          label="Using selector expression."
          code={Examples.Comparison.Range.Example2}
        />
        <CodeExample
          label={
            <>
              Using <InlineCode>For</InlineCode> function.
            </>
          }
          code={Examples.Comparison.Range.Example3}
        />
      </ValidatorFunction>
      <hr className="my-6" />
      <ValidatorFunction
        name="Compare"
        description="Ensures two resolved values from the target of selector expressions are equal."
        methods={[
          `Compare<T, P>(Expression<Func<T, P>> leftSelector, Expression<Func<T, P>> rightSelector)`,
        ]}
        brokenRule="Value must be equal to value of {rightSelector}."
      >
        <h5 className="font-semibold text-gray-700 text-sm mt-4">Example</h5>
        <CodeExample
          label="Using selector expressions."
          code={Examples.Comparison.Compare.Example1}
        />
      </ValidatorFunction>
    </Layout>
  )
}

export default ComparisonFunctions
